import { Component, Injector, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { FlowType } from '@nequi/features/commons/core';
import { DynatraceService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { CreditType } from '@nequi/features/credits/core';
import { ToastService } from '@nequi/ui';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { CreditInfoModel } from '../../../../services/credit-info.model';
import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { TermsAndConditionsConfig } from './terms-and-conditions.config';

@Component({
  selector: 'app-credits-terms-and-conditions',
  templateUrl: './terms-and-conditions.page.html',
  styleUrls: ['./terms-and-conditions.page.scss'],
})
export class TermsAndConditionsPage extends FeatureBasePage implements OnInit {
  public config = TermsAndConditionsConfig;
  public acceptAgreementCheckbox: FormControl;
  public termsAndConditions: string;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private route: ActivatedRoute,
    private creditInfoModel: CreditInfoModel,
    private toastService: ToastService,
    private creditLoggerService: CreditLoggerService,
    private dynatraceService: DynatraceService
  ) {
    super(injector);
  }

  public async ngOnInit() {
    this.setAnalyticsOfBegin();
    this.initPage();
  }

  public async ionViewWillLeave() {
    await this.dynatraceService.setAnalyticsOfEnd();
  }

  public acceptAgreement(): void {
    if (window.navigator.onLine) {
      this.goToAgreementFGA();
    }

    if (!window.navigator.onLine) {
      this.messageToastError();
    }
  }

  private goToAgreementFGA() {
    this.navigatorProvider.navigateForward(this.config.goTo.agreementFGA);
  }

  private async messageToastError(): Promise<void> {
    const message = this.translateProvider.get(this.config.i18n.messageToastError);

    this.toastService.showError(message);
  }

  private initForm(): void {
    this.acceptAgreementCheckbox = new FormControl(null, [Validators.required]);
  }

  private initData(): void {
    this.termsAndConditions = this.creditInfoModel.agreement.agreement;
  }

  private initPage(): void {
    this.initForm();
    this.initData();
    this.setLog(this.config.i18n.logs);
  }

  private async setLog(messages) {
    this.creditLoggerService.save({ flowType: FlowType.PROPELLER, i18nKey: messages });
  }

  private async setAnalyticsOfBegin() {
    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: this.config.analytics.intentionOfTermsAndConditions.name,
      eventProps: stringsArrayToObjectWithValues(
        this.config.analytics.intentionOfTermsAndConditions.params.dynatrace,
        [CreditType.PROPELLER]
      ),
    });
  }
}
