import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FlowType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { createFormat } from '@nequi/utils/string';

import { CreditInfoModel } from '../../../../services/credit-info.model';
import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { PaymentGuaranteesConfig } from './payment-guarantees.config';

@Component({
  selector: 'app-credits-payment-guarantees',
  templateUrl: './payment-guarantees.page.html',
  styleUrls: ['./payment-guarantees.page.scss'],
})
export class PaymentGuaranteesPage extends FeatureBasePage implements OnInit {
  public config = PaymentGuaranteesConfig;
  public itemList;
  private commissionAmount: string;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private route: ActivatedRoute,
    private currencyPipe: CurrencyPipe,
    private creditInfoModel: CreditInfoModel,
    private creditLoggerService: CreditLoggerService
  ) {
    super(injector);
  }

  public ngOnInit() {
    this.loadItemList();
  }

  public goToLifeInsurance() {
    this.saveLog();

    this.navigatorProvider.navigateForward(this.config.goTo.lifeInsurance);
  }

  private async loadItemList() {
    const i18n = this.translateProvider.get(this.config.i18n.content);
    const commissionPersentage = this.creditInfoModel.creditFeatureInfo.commissionPercentage;
    const amount = this.creditInfoModel.creditFeatureInfo.loanAmount;
    const p100 = 100;

    this.commissionAmount = this.currencyPipe.transform(amount * (commissionPersentage / p100));
    const newDescription = createFormat(i18n.warranty.description, [
      commissionPersentage.toString(),
      this.commissionAmount,
    ]);

    this.itemList = [
      {
        image: i18n.warranty.img,
        title: i18n.warranty.title,
        description: newDescription,
      },
      {
        image: i18n.insurance.img,
        title: i18n.insurance.title,
        description: i18n.insurance.description,
      },
    ];
  }

  private async saveLog() {
    this.creditLoggerService.save({
      flowType: FlowType.PROPELLER,
      i18nKey: this.config.i18n.logs,
      params: [
        this.creditInfoModel.creditFeatureInfo?.commissionPercentage?.toString(),
        this.commissionAmount,
      ],
    });
  }
}
