import { Component, OnInit } from '@angular/core';
import { CustomerCreditsEntity } from '@nequi/features/credits/core';

import { CreditInfoModel } from '../../../../services/credit-info.model';

@Component({
  selector: 'app-credits-propeller',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {
  public propellerData: CustomerCreditsEntity;
  public isPreapproval = false;
  public isPayment = false;

  constructor(private creditInfoModel: CreditInfoModel) {}

  public ngOnInit() {
    this.initPage();
  }

  private initPage() {
    this.propellerData = this.creditInfoModel.customerCreditInfo;
  }
}
