import { CurrencyPipe, PercentPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ScreenshotSharingService } from '@nequi/apps/base/mobile/config/features/commons/services';
import { AnalyticsInteractor, AnalyticsType, FlowType } from '@nequi/features/commons/core';
import { DynatraceService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { CreditType } from '@nequi/features/credits/core';
import { IconToastType, ToastPosition, ToastService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';
import { TranslatePipe } from '@ngx-translate/core';

import { CreditInfoModel } from '../../../../services/credit-info.model';
import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { LoanInfoConfig } from './loan-info.config';
import { LoanInfoViewModel } from './loan-info.view-model';

@Component({
  selector: 'app-loan-info-credits',
  templateUrl: './loan-info.page.html',
  styleUrls: ['./loan-info.page.scss'],
})
export class LoanInfoPage extends FeatureBasePage implements OnInit {
  public viewModel: LoanInfoViewModel;
  public config = LoanInfoConfig;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private toastService: ToastService,
    private creditInfoModel: CreditInfoModel,
    private route: ActivatedRoute,
    private percentPipe: PercentPipe,
    private screenshotSharingService: ScreenshotSharingService,
    private creditLoggerService: CreditLoggerService,
    private analyticsInteractor: AnalyticsInteractor,
    private currencyPipe: CurrencyPipe,
    private translatePipe: TranslatePipe,
    private dynatraceService: DynatraceService
  ) {
    super(injector);

    this.viewModel = new LoanInfoViewModel();
  }

  public async ngOnInit() {
    const p100 = 100;
    const {
      monthlyInsuranceAmount,
      monthlyInterestRate,
      normalAnualEfectiveRate,
    } = this.creditInfoModel.loanPortfolioInfo;

    this.viewModel.commissionPercentage =
      this.creditInfoModel.creditFeatureInfo.commissionPercentage / p100;

    this.viewModel.loanAmount = this.creditInfoModel.creditFeatureInfo.loanAmount;
    this.viewModel.monthlyFee = this.creditInfoModel.creditFeatureInfo.monthlyFee;
    this.viewModel.monthlyInsuranceAmount = monthlyInsuranceAmount;
    this.viewModel.monthlyInterestRate = monthlyInterestRate / p100;
    this.viewModel.normalAnualEfectiveRate = normalAnualEfectiveRate / p100;
    this.viewModel.term = this.creditInfoModel.creditFeatureInfo.term;
    this.viewModel.feeDay = new Date().getDate();

    await this.initI18n();
    this.saveLog();
  }

  public async shareScreenshoot() {
    await this.screenshotSharingService.viaOptions();
  }

  public async saveScreenshot() {
    await this.screenshotSharingService.viaOptions();
  }

  public goLoanDestination(): void {
    this.setAnalyticsIntentionOfSummary();

    this.navigatorProvider.navigateForward(this.config.routes.loanDestination);
  }

  public goToHelp(): void {
    this.navigatorProvider.navigateForward(this.config.routes.help, {
      queryParams: {
        context: this.config.helpContext,
      },
    });
  }

  private async initI18n(): Promise<void> {
    const howMuchIsTheInterestValue: string = this.translateProvider.get(
      this.config.i18n.loanInfo.howMuchIsTheInterestValue
    );

    this.viewModel.interestText = createFormat(howMuchIsTheInterestValue, [
      this.percentPipe.transform(this.viewModel.monthlyInterestRate, '.0-2'),
      this.percentPipe.transform(this.viewModel.normalAnualEfectiveRate, '.0-2'),
    ]);

    this.showSaveToast();
  }

  private async showSaveToast(): Promise<void> {
    const message = this.translateProvider.get(this.config.i18n.loanInfo.toatMessage);

    await this.toastService.showWithIcon({
      message,
      type: IconToastType.INFO,
      position: ToastPosition.TOP,
      cssClass: 'nequi-toast__icon--info-save-button',
      isBackdropDismiss: true,
    });
  }

  private async setAnalyticsIntentionOfSummary() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.intentionOfSummary.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.intentionOfSummary.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.intentionOfSummary.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.intentionOfSummary.params.cleverTap,
          [getFormattedDateTimes(), this.viewModel.loanAmount, this.viewModel.term.value]
        ),
      },
    });

    this.dynatraceService.setAnalyticsOfEvent({
      eventName: this.config.analytics.events.intentionOfSummary.dynatrace,
      eventProps: stringsArrayToObjectWithValues(
        this.config.analytics.events.intentionOfSummary.params.dynatrace,
        [
          CreditType.PROPELLER,
          this.viewModel.normalAnualEfectiveRate,
          this.viewModel.loanAmount,
          this.viewModel.term.value,
        ]
      ),
      leave: true,
    });
  }

  private async saveLog() {
    this.creditLoggerService.save({
      flowType: FlowType.PROPELLER,
      i18nKey: this.config.i18n.logs,
      params: [
        this.currencyPipe.transform(this.viewModel.loanAmount),
        this.currencyPipe.transform(
          this.viewModel.loanAmount +
            this.viewModel.loanAmount * this.viewModel.commissionPercentage
        ),
        this.percentPipe.transform(this.viewModel.normalAnualEfectiveRate, '.0-2'),
        this.percentPipe.transform(this.viewModel.monthlyInterestRate, '.0-2'),
        this.currencyPipe.transform(this.viewModel.monthlyInsuranceAmount),
        this.currencyPipe.transform(this.viewModel.monthlyFee),
        this.translatePipe.transform(this.config.i18n.loanInfo.whenIsEachInstallmentDebitedValue, {
          feeDay: this.viewModel.feeDay,
        }),
        this.viewModel.term.name,
      ],
    });
  }
}
