import { FormControl, FormGroup, Validators } from '@angular/forms';
import { GetAdditionalInfoEntity } from '@nequi/features/credits/core';

export class LoanDestinationViewModel {
  public form: FormGroup;
  public destination: FormControl;
  public additionalInfo: GetAdditionalInfoEntity;

  public async initForm(): Promise<void> {
    this.destination = new FormControl(null, [Validators.required]);

    this.form = new FormGroup({
      destination: this.destination,
    });
  }
}
