import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FlowType } from '@nequi/features/commons/core';
import { DynatraceService, FeatureBasePage } from '@nequi/features/commons/presentation';

import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { ThisGotSeriousConfig } from './this-got-serious.config';

@Component({
  selector: 'app-credits-this-got-serious',
  templateUrl: './this-got-serious.page.html',
  styleUrls: ['./this-got-serious.page.scss'],
})
export class ThisGotSeriousPage extends FeatureBasePage implements OnInit {
  public config = ThisGotSeriousConfig;

  constructor(
    injector: Injector,
    private route: ActivatedRoute,
    private creditLoggerService: CreditLoggerService,
    private dynatraceService: DynatraceService
  ) {
    super(injector);
  }

  public async ngOnInit() {
    await this.dynatraceService.setAnalyticsOfEnd();

    this.setLog(this.config.i18n.logs);
  }

  public goToTermsAndConditions() {
    this.navigatorProvider.navigateForward(this.config.goTo.termsAndConditions);
  }

  private async setLog(messages) {
    this.creditLoggerService.save({ flowType: FlowType.PROPELLER, i18nKey: messages });
  }
}
