import { Injectable } from '@angular/core';
import { BaseBottomSheetService, BottomSheetController, Theme } from '@nequi/ui';

import { AddMoreInfoOrganism } from '../add-more-info.organism';
import { AddMoreInfoProps } from '../entities/add-more-info-props.interface';

@Injectable({ providedIn: 'root' })
export class AddMoreInfoBottomSheetService
  implements BaseBottomSheetService<Partial<AddMoreInfoProps>, boolean> {
  constructor(private bottomSheetController: BottomSheetController) {}

  public async show(componentProps: Partial<AddMoreInfoProps>) {
    const bottomSheet = this.bottomSheetController.create({
      isAutoHeight: true,
      component: AddMoreInfoOrganism,
      componentProps,
      cssClass:
        componentProps?.theme === Theme.DARK || !componentProps?.theme
          ? 'bottom-sheet--medianoche'
          : '',
    });

    await bottomSheet.open();

    return bottomSheet.onDidDismiss();
  }

  public hide() {
    return this.bottomSheetController.dismiss();
  }
}
