import { CurrencyPipe } from '@angular/common';
import { Component, OnInit, ViewChild } from '@angular/core';
import {
  BarcodeInformationEntity,
  BarcodeStatus,
  FlowType,
  IncomeExpensesEntity,
  LocationEntity,
  LoggerInteractor,
  ModuleCode,
  ModuleName,
  OccupationEntity
} from '@nequi/features/commons/core';
import { AlertService, DynatraceService } from '@nequi/features/commons/presentation';
import {
  NavigatorProvider,
  TranslateProvider,
  UserInfoProvider
} from '@nequi/features/commons/providers';
import { CreditType, SARLAFT_LOCKS } from '@nequi/features/credits/core';
import { ModuleInfoInteractor } from '@nequi/features/profile/core';
import { CompletedModuleData, ModuleListOrganism } from '@nequi/features/profile/presentation';
import { LoadingService, ToastService } from '@nequi/ui';
import { openUrl, stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { ConfigCreditProvider } from '../../../../config/credits.config';
import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { CustomerFinancialService } from '../../../../services/customer-finacial.service';
import { KnowUsBetterConfig } from './know-us-better.config';

@Component({
  selector: 'app-credits-know-us-better',
  templateUrl: './know-us-better.page.html',
})
export class KnowUsBetterPage implements OnInit {
  @ViewChild('nequiPropellerKnowUsBetterModuleList')
  private moduleListOrganism: ModuleListOrganism;

  public config = KnowUsBetterConfig;
  private moduleListData: CompletedModuleData[];
  private locationData: LocationEntity;
  private occupationData: OccupationEntity;
  private incomeAndExpensesData: IncomeExpensesEntity;

  constructor(
    private translateProvider: TranslateProvider,
    private creditLoggerService: CreditLoggerService,
    private moduleInfoInteractor: ModuleInfoInteractor,
    private loggerInteractor: LoggerInteractor,
    private toastService: ToastService,
    private alertService: AlertService,
    private navigatorProvider: NavigatorProvider,
    private configCreditProvider: ConfigCreditProvider,
    private userInfoProvider: UserInfoProvider,
    private currencyPipe: CurrencyPipe,
    private customerFinancialService: CustomerFinancialService,
    private loadingService: LoadingService,
    private dynatraceService: DynatraceService
  ) {}

  public ngOnInit() {
    this.setAnalyticsOfBegin();
  }

  public async goBack() {
    await this.dynatraceService.setAnalyticsOfEnd();
    this.navigatorProvider.back();
  }

  public goToHelp() {
    openUrl(this.config.goTo.help);
  }

  public onCompletedRequestData(modules: CompletedModuleData[]) {
    this.moduleListData = modules;

    const incomeExpenses = modules.find((module) => module.type === ModuleCode.INCOME_EXPENSES)
      .data as IncomeExpensesEntity;

    if (incomeExpenses) {
      this.setCreditUserInfoAsset(incomeExpenses);
    }

    if (this.canDoSarlaft()) {
      this.setLog();
    }
  }

  public onCompletedModuleData(module: CompletedModuleData) {
    if (module.type === ModuleCode.LOCATION) {
      this.locationCompletedHandler(module.data as LocationEntity);
    }

    if (module.type === ModuleCode.OCCUPATION) {
      this.occupationCompletedHandler(module.data as OccupationEntity);
    }

    if (module.type === ModuleCode.INCOME_EXPENSES) {
      this.incomeAndExpensesCompletedHandler(module.data as IncomeExpensesEntity);
    }
  }

  private setCreditUserInfoAsset(incomeExpenses: IncomeExpensesEntity) {
    this.customerFinancialService.updateCreditUser({
      ...this.customerFinancialService.creditUserInfo,
      assets: incomeExpenses.incomeExpenses?.creditDebit?.credit,
    });
  }

  private async incomeAndExpensesCompletedHandler(incomeAndExpenses: IncomeExpensesEntity) {
    try {
      await this.moduleInfoInteractor.saveModuleInfo<IncomeExpensesEntity>({
        code: ModuleCode.INCOME_EXPENSES,
        data: { key: ModuleName.INCOME_EXPENSES, value: incomeAndExpenses },
      });

      this.setCreditUserInfoAsset(incomeAndExpenses);

      if (await this.sarlaftHandler()) {
        this.moduleListOrganism.finish();
      }
    } catch (error) {
      this.dynatraceService.setCustomError(error);
      this.loggerInteractor.error(error);

      await this.toastService.showError(error.message);
    }
  }

  private async locationCompletedHandler(location: LocationEntity) {
    try {
      await this.moduleInfoInteractor.saveModuleInfo<LocationEntity>({
        code: ModuleCode.LOCATION,
        data: { key: ModuleName.LOCATION, value: location },
      });

      if (await this.sarlaftHandler()) {
        this.moduleListOrganism.finish();
      }
    } catch (error) {
      this.dynatraceService.setCustomError(error);
      this.loggerInteractor.error(error);

      await this.toastService.showError(error.message);
    }
  }

  private async occupationCompletedHandler(occupation: OccupationEntity) {
    try {
      await this.moduleInfoInteractor.saveModuleInfo<OccupationEntity>({
        code: ModuleCode.OCCUPATION,
        data: { key: ModuleName.OCCUPATION, value: occupation },
      });

      if (await this.sarlaftHandler()) {
        this.moduleListOrganism.finish();
      }
    } catch (error) {
      this.dynatraceService.setCustomError(error);
      this.loggerInteractor.error(error);

      await this.toastService.showError(error.message);
    }
  }

  private async lockedInSarlaftError() {
    const message = this.translateProvider.get(this.config.i18n.sarlaft.alert.lockedInSarlaftError);

    await this.alertService.show({
      messages: {
        header: message.header,
        message: message.message,
        buttons: message.buttons,
      },
      handlers: {
        accept: () => this.navigatorProvider.navigateRoot([this.config.goTo.backpack]),
      },
      backdropDismiss: false,
    });
  }

  private canDoSarlaft(): boolean {
    let canDo = false;
    const documentData = this.moduleListOrganism.getModuleData<BarcodeStatus>(
      this.moduleListData,
      ModuleCode.CAPTURE_DOCUMENT
    );

    this.locationData = this.moduleListOrganism.getModuleData<LocationEntity>(
      this.moduleListData,
      ModuleCode.LOCATION
    );

    this.occupationData = this.moduleListOrganism.getModuleData<OccupationEntity>(
      this.moduleListData,
      ModuleCode.OCCUPATION
    );

    this.incomeAndExpensesData = this.moduleListOrganism.getModuleData<IncomeExpensesEntity>(
      this.moduleListData,
      ModuleCode.INCOME_EXPENSES
    );

    const isDocumentValid = documentData && !documentData?.error;

    canDo = !!(
      isDocumentValid &&
      this.locationData &&
      this.occupationData &&
      this.incomeAndExpensesData
    );

    return canDo;
  }

  private async sarlaftHandler(): Promise<boolean> {
    try {
      await this.loadingService.show();

      if (this.canDoSarlaft()) {
        const occupationCode = this.occupationData?.ciiuCode?.key
          ? this.occupationData?.ciiuCode?.key
          : this.occupationData?.occupation?.name.toUpperCase();

        await this.moduleInfoInteractor.calculateClientRisk({
          cityCode: this.locationData?.city?.code,
          countryCode: this.configCreditProvider.countryCode,
          incomes: this.incomeAndExpensesData?.incomeExpenses?.income?.max,
          occupationCode: occupationCode,
        });

        return true;
      }

      return true;
    } catch (error) {
      this.manageErrors(error);
    } finally {
      await this.loadingService.hideWithoutFeedback();
    }
  }

  private async manageErrors(error) {
    this.dynatraceService.setCustomError(error);

    if (SARLAFT_LOCKS.includes(error.code)) {
      await this.lockedInSarlaftError();

      return;
    }

    await this.toastService.showError(error.message);
  }

  private setLog() {
    const barcodeInfo = this.moduleListOrganism.getModuleData<BarcodeInformationEntity>(
      this.moduleListData,
      ModuleCode.BARCODE_INFORMATION
    );

    const address = this.locationData.address;
    const birthDate = barcodeInfo?.birthDate?.replace(this.config.regex.dateformat, '$1-$2-$3');
    const params = [
      barcodeInfo?.documentNumber?.toString(),
      birthDate,
      this.userInfoProvider.getEmail(),
      this.locationData.province.name,
      this.locationData.city.name,
      `${address.road.alias} ${address.firstPart} ${address.secondPart}`,
      this.occupationData.occupation.name,
      this.incomeAndExpensesData.incomeExpenses?.income?.description,
      this.incomeAndExpensesData.incomeExpenses?.expenses?.description,
      this.currencyPipe.transform(
        this.incomeAndExpensesData.incomeExpenses?.creditDebit?.credit.toString()
      ),
      this.currencyPipe.transform(
        this.incomeAndExpensesData.incomeExpenses?.creditDebit?.debit.toString()
      ),
      this.incomeAndExpensesData.incomeExpenses?.originMoney?.name,
    ];

    this.creditLoggerService.save({
      flowType: FlowType.PROPELLER,
      i18nKey: this.config.i18n.logs,
      params: params,
    });
  }

  private async setAnalyticsOfBegin() {
    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: this.config.analytics.intentionOfModules.name,
      eventProps: stringsArrayToObjectWithValues(
        this.config.analytics.intentionOfModules.params.dynatrace,
        [CreditType.PROPELLER]
      ),
    });
  }
}
