/* eslint-disable sonarjs/no-duplicate-string */
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';
import { FlowType } from '@nequi/features/commons/core';

import { BaseConfig } from '../../middlewares/base.config';
import { MiddlewareModuleName } from '../../middlewares/base.middleware';
import { PropellerMiddleware } from '../../middlewares/propeller.middleware';
import {
  OccupationEnginePage
} from '../../organisms/approval-front/pages/occupation-engine/occupation-engine.page';
import {
  YourMoneyEnginePage
} from '../../organisms/approval-front/pages/your-money-engine/your-money-engine.page';
import { PropellerConfig } from './config/propeller.config';
import { AdjustIntallmentsPage } from './pages/adjust-installments/adjust-installments.page';
import { AgreementFGAPage } from './pages/agreement-fga/agreement-fga.page';
import { ChangeLiveInsurancePage } from './pages/change-live-insurance/change-live-insurance.page';
import { EnsureLoanPage } from './pages/ensure-loan/ensure-loan.page';
import { HomePage } from './pages/home/home.page';
import { KnowUsBetterEnginePage } from './pages/know-us-better-engine/know-us-better-engine.page';
import { KnowUsBetterPage } from './pages/know-us-better/know-us-better.page';
import { LifeInsurancePage } from './pages/life-insurance/life-insurance.page';
import { LoanDestinationPage } from './pages/loan-destination/loan-destination.page';
import { LoanInfoPage } from './pages/loan-info/loan-info.page';
import { PaymentDetailPage } from './pages/payment-detail/payment-detail.page';
import { PaymentGuaranteesPage } from './pages/payment-guarantees/payment-guarantees.page';
import { PaymentHomePage } from './pages/payment-home/payment-home.page';
import { PreapprovedHomePage } from './pages/preapproved-home/preapproved-home.page';
import { TermsAndConditionsPage } from './pages/terms-and-conditions/terms-and-conditions.page';
import { ThisGotSeriousPage } from './pages/this-got-serious/this-got-serious.page';

const routes: Routes = [
  {
    path: '',
    canActivate: [PropellerMiddleware],
    data: {
      moduleName: MiddlewareModuleName.PROPELLER,
      bottomSheetKey: BaseConfig.propeller.bottomSheet,
      redirectToPayment: BaseConfig.routes.propellerPayment,
      redirectToPreapproved: BaseConfig.routes.propellerPreapproved,
      flowType: FlowType.PROPELLER,
    },
    component: HomePage,
  },
  {
    path: 'payment-guarantees',
    component: PaymentGuaranteesPage,
  },
  {
    path: 'life-insurance',
    component: LifeInsurancePage,
  },
  {
    path: 'ensure-loan',
    component: EnsureLoanPage,
  },
  {
    path: 'loan-info',
    component: LoanInfoPage,
  },
  {
    path: 'know-us-better',
    component: KnowUsBetterPage,
  },
  {
    path: 'loan-destination',
    component: LoanDestinationPage,
  },
  {
    path: 'this-got-serious',
    component: ThisGotSeriousPage,
  },
  {
    path: 'terms-and-conditions',
    component: TermsAndConditionsPage,
  },
  {
    path: 'agreement-fga',
    component: AgreementFGAPage,
  },
  {
    path: 'payment-detail',
    component: PaymentDetailPage,
  },
  {
    path: 'adjust-installments',
    component: AdjustIntallmentsPage,
  },
  {
    path: 'know-us-better-engine',
    component: KnowUsBetterEnginePage,
  },
  {
    path: 'change-live-insurance',
    component: ChangeLiveInsurancePage,
  },
  {
    path: 'payment-home',
    component: PaymentHomePage,
  },
  {
    path: 'preapproved-home',
    component: PreapprovedHomePage,
  },
  {
    path: 'occupation-engine',
    component: OccupationEnginePage,
  },
  {
    path: 'your-money-engine',
    component: YourMoneyEnginePage,
  },
  {
    path: 'profile',
    loadChildren: () =>
      import('@nequi/features/profile/presentation').then((m) => m.FeaturesProfileModule),
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(
      I18nModuleResolver.setup(routes, [
        PropellerConfig.i18n.moduleKey,
        PropellerConfig.i18n.contractModuleKey,
      ])
    ),
  ],
  exports: [RouterModule],
})
export class PropellerRoutingModule {}
