import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FlowType } from '@nequi/features/commons/core';
import { DynatraceService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { CustomerCreditsEntity } from '@nequi/features/credits/core';

import {
  ApprovalFrontViewModel
} from '../../../../organisms/approval-front/approval-front.view-model';
import { PreapprovalOrganism } from '../../../../organisms/preapproval/preapproval.organism';
import { PreapprovalViewModel } from '../../../../organisms/preapproval/preapproval.view-model';
import { CreditInfoModel } from '../../../../services/credit-info.model';
import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { PropellerConfig } from './preapproved-home.config';

@Component({
  selector: 'app-credits-propeller',
  templateUrl: './preapproved-home.page.html',
  styleUrls: ['./preapproved-home.page.scss'],
})
export class PreapprovedHomePage extends FeatureBasePage implements OnInit, OnDestroy {
  @ViewChild(PreapprovalOrganism)
  public preaprovalOrganism: PreapprovalOrganism;

  public propellerData: CustomerCreditsEntity;

  public isPreapproval = true;
  public config = PropellerConfig;

  constructor(
    injector: Injector,
    private creditInfoModel: CreditInfoModel,
    public viewModel: PreapprovalViewModel,
    private route: ActivatedRoute,
    private creditLoggerService: CreditLoggerService,
    private currencyPipe: CurrencyPipe,
    private dynatraceService: DynatraceService,
    public approvalFrontViewModel: ApprovalFrontViewModel
  ) {
    super(injector);
  }

  public ngOnInit() {
    this.initPage();
  }

  public async ngOnDestroy() {
    this.approvalFrontViewModel.resetValue();
  }

  public async ionViewWillLeave() {
    await this.dynatraceService.setAnalyticsOfEnd();
  }

  public async goToPaymentGuarantees() {
    this.saveLog();
    this.navigatorProvider.navigateForward(this.config.routes.paymentGuarantees);
  }

  public goBack() {
    if (this.approvalFrontViewModel.info.isFromApproval) {
      this.navigatorProvider.back();

      return;
    }

    super.goBack();
  }

  public onShowHelpPressed() {
    this.navigatorProvider.navigateForward(this.config.routes.help, {
      queryParams: {
        context: this.config.i18n.helpContext,
      },
    });
  }

  private initPage() {
    this.propellerData = this.creditInfoModel.customerCreditInfo;
  }

  private saveLog(): void {
    this.creditLoggerService.save({
      flowType: FlowType.PROPELLER,
      i18nKey: this.config.i18n.logs.amount,
      params: [
        this.currencyPipe.transform(this.viewModel.maxAmount),
        this.currencyPipe.transform(this.creditInfoModel.creditFeatureInfo.loanAmount),
        this.creditInfoModel.creditFeatureInfo.installment.toString(),
        this.currencyPipe.transform(this.creditInfoModel.creditFeatureInfo.monthlyFee),
      ],
    });
  }
}
