import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FeatureBasePage, Gender } from '@nequi/features/commons/presentation';

import { CustomerFinancialService } from '../../../../services/customer-finacial.service';
import { LifeInsuranceConfig } from './life-insurance.config';
import { LifeInsuranceViewModel } from './life-insurance.view-model';

@Component({
  selector: 'app-life-insurance-credits',
  templateUrl: './life-insurance.page.html',
  styleUrls: ['./life-insurance.page.scss'],
})
export class LifeInsurancePage extends FeatureBasePage implements OnInit {
  public viewModel: LifeInsuranceViewModel;
  public config = LifeInsuranceConfig;
  public gender = Gender;

  constructor(
    injector: Injector,
    private route: ActivatedRoute,
    private customerFinancialService: CustomerFinancialService
  ) {
    super(injector);
    this.viewModel = new LifeInsuranceViewModel();
  }

  public ngOnInit() {
    this.initPage();
  }

  public goToEnsureLoan() {
    this.customerFinancialService.updateCreditUser({
      ...this.customerFinancialService.creditUserInfo,
      gender: this.viewModel.gender.value,
    });

    this.navigatorProvider.navigateForward(this.config.goTo.ensureLoan);
  }

  private initPage(): void {
    this.viewModel.initForm();
  }
}
