import { Component, Injector, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { FlowType } from '@nequi/features/commons/core';
import { DynatraceService, FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  CreditInteractor,
  CreditType,
  DontHaveDataException,
  GetAdditionalInfoEntity
} from '@nequi/features/credits/core';
import { LoadingService, Theme, ToastService } from '@nequi/ui';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import {
  AddMoreInfoProps
} from '../../../../organisms/add-more-info/entities/add-more-info-props.interface';
import {
  AddMoreInfoBottomSheetService
} from '../../../../organisms/add-more-info/services/add-more-info-bottom-sheet.service';
import { CreditInfoModel } from '../../../../services/credit-info.model';
import { LoanDestinationConfig } from './loan-destination.config';
import { LoanDestinationViewModel } from './loan-destination.view-model';

const REGISTRATION_STATUS = 0;

@Component({
  selector: 'app-loan-destination-credits',
  templateUrl: './loan-destination.page.html',
  styleUrls: ['./loan-destination.page.scss'],
})
export class LoanDestinationPage extends FeatureBasePage implements OnInit {
  public viewModel: LoanDestinationViewModel;
  public config = LoanDestinationConfig;

  constructor(
    injector: Injector,
    private route: ActivatedRoute,
    private addMoreInfoBottomSheetService: AddMoreInfoBottomSheetService,
    private creditInfoModel: CreditInfoModel,
    private creditInteractor: CreditInteractor,
    private loadingService: LoadingService,
    private toastService: ToastService,
    private dynatraceService: DynatraceService
  ) {
    super(injector);
    this.viewModel = new LoanDestinationViewModel();
  }

  public ngOnInit() {
    this.initPage();
  }

  public async ionViewWillLeave() {
    await this.dynatraceService.setAnalyticsOfEnd();
  }

  public async continueToKnowUsBetter() {
    if (await this.onSaveAdditionalInfo()) {
      this.creditInfoModel.creditFeatureInfo = {
        ...this.creditInfoModel.creditFeatureInfo,
        loanDestination: this.viewModel.destination.value,
      };

      this.goToKnowUsBetter();
    }
  }

  public openAddMoreInfo(): void {
    const componentProps: Partial<AddMoreInfoProps> = {
      title: this.config.i18n.loanDestination.bottomSheet.title,
      label: this.config.i18n.loanDestination.bottomSheet.label,
      actionLabel: this.config.i18n.loanDestination.bottomSheet.actionLabel,
      action: this.addMoreInfoAction.bind(this),
      theme: Theme.LIGHT,
    };

    this.addMoreInfoBottomSheetService
      .show(componentProps)
      .then(() => this.viewModel.destination.setValue(null));
  }

  private async initPage() {
    this.viewModel.initForm();

    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: this.config.analytics.intentionOfDestination.name,
    });

    this.onGetAdditionalInfo();
  }

  private goToKnowUsBetter() {
    this.navigatorProvider.navigateForward(this.config.routes.knowUsBetter);
  }

  private addMoreInfoAction(_form: FormGroup): void {
    const { moreInfo } = _form.value;

    this.viewModel.destination.setValue(moreInfo);
    this.addMoreInfoBottomSheetService.hide();
    this.continueToKnowUsBetter();
  }

  private async onGetAdditionalInfo() {
    try {
      this.loadingService.show();

      const resultInfo: GetAdditionalInfoEntity[] = await this.creditInteractor.getAdditionalInfo({
        registrationStatus: REGISTRATION_STATUS,
        flow: FlowType.PROPELLER,
      });

      if (resultInfo.length > 0) {
        this.viewModel.additionalInfo = resultInfo[0];
        this.viewModel.destination.setValue(this.viewModel.additionalInfo.purpose);
      }

      this.loadingService.hideWithoutFeedback();
    } catch (error) {
      this.dynatraceService.setCustomError(error);

      if (error.constructor !== DontHaveDataException) {
        await this.toastService.showError(error.message);

        return;
      }
    } finally {
      this.loadingService.hideWithoutFeedback();
    }
  }

  private async onSaveAdditionalInfo(): Promise<boolean> {
    try {
      this.loadingService.show();

      await this.creditInteractor.saveAdditionalInfo({
        registrationStatus:
          this.viewModel.additionalInfo?.registrationStatus ?? REGISTRATION_STATUS,
        flow: FlowType.PROPELLER,
        options: [{ key: this.config.keyOptions.purpose, value: this.viewModel.destination.value }],
      });

      await this.setAnalyticsOfProperties([CreditType.PROPELLER, this.viewModel.destination.value]);

      this.loadingService.hideWithoutFeedback();

      return true;
    } catch (error) {
      this.dynatraceService.setCustomError(error);
      await this.toastService.showError(error.message);

      return false;
    } finally {
      this.loadingService.hideWithoutFeedback();
    }
  }

  private async setAnalyticsOfProperties(eventProps) {
    const props = stringsArrayToObjectWithValues(
      this.config.analytics.intentionOfDestination.params.dynatrace,
      [...eventProps]
    );

    this.dynatraceService.setAnalyticsOfProperties({ props });
  }
}
