import { Injectable } from '@angular/core';
import {
  CONFIDENCE_SCORE_CREDITS_ERRORS,
  CreditType,
  CUSTOMER_MUST_GO_TO_ENGINE,
  CustomerCreditsEntity,
  CUSTORMER_WITHOUT_PREAPROVED_ERRORS,
  FgaRegulationLockoutException
} from '@nequi/features/credits/core';

import { BaseMiddleware } from './base.middleware';
import { ModalParams } from './modal.params';

@Injectable()
export class PropellerMiddleware extends BaseMiddleware {
  protected async getCustomerCreditsFail(error): Promise<boolean> {
    this.dynatraceService.setCustomError(error);

    let result = false;

    if (CUSTOMER_MUST_GO_TO_ENGINE.includes(error.code)) {
      if (error.data) {
        this.setCustomerCreditsError(error.data);
      }

      result = await this.showWelcome();

      return result ? this.showUserDontHavePreapprovalModal() : result;
    }

    if (CONFIDENCE_SCORE_CREDITS_ERRORS.includes(error.code)) {
      await this.onSarlaftLocksError();

      return;
    }

    if (CUSTORMER_WITHOUT_PREAPROVED_ERRORS.includes(error.code)) {
      result = await this.showWelcome();

      return result ? this.showOtherCreditOptions(error.data) : result;
    }

    // TODO: Queda pendiente un refactor para incluir los errores anteriores (No se hace para evitar regresion en el HU)
    if (this.getCustomerCreditException[error.constructor.name]) {
      return this.getCustomerCreditException[error.constructor.name](error);
    }

    await this.showComeBackLaterAlert(error.message);

    return result;
  }

  protected setGetCustomerCreditsException() {
    this.getCustomerCreditException = {
      [FgaRegulationLockoutException.name]: async () => this.onFgaRegulationLockoutError(),
    };
  }

  protected async showOtherCreditOptions(data: CustomerCreditsEntity): Promise<boolean> {
    let i18nKey = this.config.propeller.modal.propeller;
    let url: string;

    const [creditType] = data?.availableProducts || [];

    if (creditType === CreditType.LIFEGUARD) {
      i18nKey = this.config.propeller.modal.lifeguard;
      url = this.config.routes.lifeguard;
    }

    return this.showCardModal<boolean>({
      i18nKey: i18nKey,
      onAccept: () => (url ? this.navController.navigateRoot(url) : false),
      onCancel: () => false,
    } as ModalParams);
  }

  private async onFgaRegulationLockoutError(): Promise<boolean> {
    const i18nKey = this.config.propeller.modal.fgaRegulationLockout;

    return this.showCardModal<boolean>({
      i18nKey: i18nKey,
      onAccept: () => this.navController.navigateRoot(this.config.routes.dashboard),
      onCancel: () => false,
    } as ModalParams);
  }
}
