import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { FlowType } from '@nequi/features/commons/core';
import { DynatraceService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { CreditType, RescheduleType } from '@nequi/features/credits/core';
import { PresentationPocket } from '@nequi/features/money-management/presentation';
import { LoadingService } from '@nequi/ui';
import { ConfirmInfoBottomSheetService } from '@nequi/ui/services';
import { stringsArrayToObjectWithValues, toCamelCaseProperties } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { BuildItemsParams } from '../../../../params/build-items.params';
import { AdjustInstallmentsService } from '../../../../services/adjust-installments.service';
import { AdjustIntallmentsConfig } from './adjust-installments.config';
import { AdjustIntallmentsViewModel } from './adjust-installments.view-model';

@Component({
  selector: 'app-adjust-installments-credits',
  templateUrl: './adjust-installments.page.html',
  styleUrls: ['./adjust-installments.page.scss'],
})
export class AdjustIntallmentsPage extends FeatureBasePage implements OnInit {
  public config = AdjustIntallmentsConfig;
  public viewModel: AdjustIntallmentsViewModel;
  public i18n: Record<string, any>;
  public flowType = FlowType;
  public pocket: PresentationPocket;

  constructor(
    injector: Injector,
    public translateProvider: TranslateProvider,
    public adjustInstallmentsService: AdjustInstallmentsService,
    private currencyPipe: CurrencyPipe,
    private datePipe: DatePipe,
    protected confirmBottomSheetService: ConfirmInfoBottomSheetService,
    private loadingService: LoadingService,
    private dynatraceService: DynatraceService,
    private router: Router
  ) {
    super(injector);
    this.viewModel = new AdjustIntallmentsViewModel();
  }

  public async ngOnInit() {
    this.setPocket();
    this.initI18n();

    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: this.config.analytics.intentionToPayment.name,
    });

    await this.loadData();
  }

  public async ionViewWillLeave() {
    await this.dynatraceService.setAnalyticsOfEnd();
  }

  public async loadData() {
    try {
      this.viewModel.hasError = false;

      await this.loadingService.show();
      await this.adjustInstallmentsService.loadData();
      await this.loadingService.hide(true);
    } catch (error) {
      await this.loadingService.hide(false);

      this.viewModel.hasError = true;
      this.viewModel.bussyIndicatorError = error.message;

      this.dynatraceService.setCustomError(error);
    }
  }

  public async showConfirmationInfo(rescheduleType: RescheduleType) {
    this.viewModel.rescheduleType = rescheduleType;

    const buildItemParams: BuildItemsParams = {
      messages: this.i18n.confirmation,
      pocketInfo: this.pocket,
      currencyPipe: this.currencyPipe,
      datePipe: this.datePipe,
      paymentAmount: this.adjustInstallmentsService.paymentAmount,
      nextFeeDate: this.adjustInstallmentsService.data.nextFeeDate,
    };

    const { data } = await this.confirmBottomSheetService.show({
      title: this.i18n.confirmation.title,
      subtitle: this.i18n.confirmation.subtitle,
      items: this.viewModel.buildItems(buildItemParams),
      confirmLabel: this.i18n.confirmation.buttons.next,
      cancelLabel: this.i18n.confirmation.buttons.cancel,
      viewCancel: true,
      footerDescription: createFormat(this.i18n.confirmation.footerDescription, [
        this.datePipe.transform(this.adjustInstallmentsService.data.lastFeeDate),
      ]),
    });

    if (data === true) {
      this.adjustInstallmentsService.makePayment(
        this.i18n,
        this.viewModel.rescheduleType,
        this.pocket,
        CreditType.PROPELLER
      );
    }
  }

  public async onAccept(params: {
    isSuccess: boolean;
    rescheduleType: RescheduleType;
    error: CoreException;
  }) {
    if (params.isSuccess) {
      this.showConfirmationInfo(params.rescheduleType);
      this.setAnalyticsOfProperties(params.rescheduleType);

      return;
    }

    this.dynatraceService.setCustomError(params.error);
  }

  private initI18n() {
    const result = this.translateProvider.get(this.config.i18n.content);

    this.i18n = toCamelCaseProperties(result);
  }

  private async setAnalyticsOfProperties(rescheduleType: RescheduleType) {
    const paramsValue = [
      CreditType.PROPELLER,
      this.adjustInstallmentsService.paymentAmount,
      this.translateProvider.get(this.config.i18n.paymentType[rescheduleType]),
      this.adjustInstallmentsService.data.nextFeeDate,
    ].concat(
      rescheduleType === RescheduleType.INSTALLMENTS_AMOUNT
        ? [this.adjustInstallmentsService.data.amountFeesAdjusted]
        : [
            this.adjustInstallmentsService.data.numberFeesAdjusted,
            this.adjustInstallmentsService.data.lastFeeDate,
          ]
    );

    const props = stringsArrayToObjectWithValues(
      [...this.config.analytics.intentionToPayment.params.dynatrace].concat(
        this.config.analytics.intentionToPayment.params[rescheduleType]
      ),
      paramsValue
    );

    this.dynatraceService.setAnalyticsOfProperties(props);
  }

  private setPocket() {
    this.pocket = this.router.getCurrentNavigation().extras.state?.pocket;
  }
}
